import { Component, Input } from '@angular/core';
import { PriorAuthItem, PriorAuthMilestone } from '../../../../../models/prior-auth.model';
import { NzModalService } from 'ng-zorro-antd/modal';
import { GlidianProcessViewComponent } from './glidian-process-view.component';

@Component({
  selector: 'app-glidian-process-milestone',
  templateUrl: './glidian-process-milestone.component.html',
  styleUrls: ['./glidian-process-milestone.component.scss']
})
export class GlidianProcessMilestoneComponent {
  @Input() milestone!: PriorAuthMilestone;
  @Input() worklistItem!: PriorAuthItem;

  constructor(private modal: NzModalService) {}

  getStepDetail() {
    return this.milestone.milestoneData.find(md => md.type === 'CURRENT_STEP');
  }

  getStepTagColor(step: { valueText: string }): string {
    switch (step.valueText) {
      case 'Rejected':
        return 'error';
      case 'More Info':
        return 'warning';
      case 'Submitted':
        return 'default';
      case 'Received':
        return 'success';
      case 'Preparing':
        return 'default';
      // Added 'Queued' step: uses default color
      case 'Queued':
        return 'default';
      default:
        return 'processing';
    }
  }

  getStepIcon(step: { valueText: string }): string {
    switch (step.valueText) {
      case 'Received':
        return 'check-circle';
      case 'More Info':
        return 'warning';
      case 'Submitted':
        return 'cloud-upload';
      case 'Preparing':
        return 'folder-open';
      case 'Rejected':
        return 'close-circle';
      // Added 'Queued' step: uses hourglass icon
      case 'Queued':
        return 'hourglass';
      default:
        return 'sync';
    }
  }

  openMilestoneModal(): void {
    this.modal.create({
      nzTitle: 'Glidian Milestone Details',
      nzContent: GlidianProcessViewComponent,
      nzData: {
        milestone: this.milestone,
        worklistItem: this.worklistItem
      },
      nzFooter: null,
      nzWidth: 800
    });
  }

} 